.navbar{
    background: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.menu{
    height: 100%;
    display: flex;
    align-items: center;
}

.menuItem{
    margin: 1rem;
    cursor: pointer;
    font-size: larger;
}

.menuItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.menuBtn{
    background: aqua;
    border: none;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    cursor: pointer;
}

.menuBtnImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}

.active{
    color: yellow;
    border-bottom: 3px solid yellow;
}

.sMenu{
    position:absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.sMenuItem{
    color: aqua;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

.mobMenu{
    object-fit: cover;
    height: 1.8rem;
    display: none;
}

@media screen and (max-width:720px) {
    .mobMenu{
        display: flex;
    }

    .menu{
        display: none;
    }

    .menuBtn{
        display: none;
    }
}